.Founders {
  margin: 50px auto 100px auto;
  height: 300px;
  background-color: #2c2c2c;
  width: 60%;
  display: grid;
  padding: 50px 0;
  grid-template-columns: 1fr 1fr 1fr;
  border-radius: 50px 0 50px 0;

  &--photo {
    height: 150px;
    width: 150px;
    border: 3px solid grey;
    margin: 0 auto;
    border-radius: 200px;
    overflow: hidden;
    transition: all;
    transition-duration: 1s;

    &:hover {
      border: 3px solid #f65354;
    }

    & > img {
      transition: all;
      transition-duration: 5s;

      filter: grayscale(0%);
      &:hover {
        transform: scale(1.5);
        filter: grayscale(100%);
      }
    }
  }

  &--info {
    margin: 20px 0 20px 0;
    color: #fd4d4d;
    font-size: 15px;
    font-weight: 550;
  }

  &--subinfo {
    margin-top: 5px;
    color: rgb(218, 218, 218);
    font-size: 12px;
  }
}

@media only screen and (max-width: 600px) {
  .Founders {
    width: 80%;
    height: fit-content;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}
